import React from "react";
import { useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
//selectors
import { themeState } from "../../../redux/reducers/theme/selectors";
//actions
import { setMenu } from "../../../redux/actions/theme";
//hooks
import { useCurrentLocation } from "../../../hooks";
//components
import { IconButton } from "../../../component";
//assets
import { logo, notification, wallet } from "../../../assets/icons";
import { Burger, DownArrow } from "../../../assets/svg";
//styles
import "./styles.scss";

const HeaderApp = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const currentRoute = useCurrentLocation()[2];
  const { openMenu } = useSelector(themeState);

  const toggleMenu = () => {
    dispatch(setMenu(!openMenu));
  };
  const navigationPools = () => {
    history.push({ pathname: "/app/pools" });
  };

  return (
    <div className="header">
      <div className="header__titleTablet">
        <div className="header__burger" onClick={toggleMenu}>
          <Burger />
        </div>
        <img src={logo} alt="ThorChain" />
      </div>
      <div className="header__title">
        {currentRoute === "pool" ? (
          <>
            <div className="header__back" onClick={navigationPools}>
              <DownArrow />
            </div>
            All pools
          </>
        ) : (
          currentRoute
        )}
      </div>
      <div className="header__right">
        <div className="header__notification">
          <img src={notification} alt="notification" />
        </div>
        <div className="header__line" />
        <div className="header__wallet">
          <IconButton  title="Connect" icon={wallet} />
        </div>
      </div>
    </div>
  );
};

export default HeaderApp;
