import React from "react";
import { useHistory } from "react-router-dom";
//utils
import { setToken } from "../utils/localStorage";

const Home = () => {
  const history = useHistory();
  const onApp = () => {
    setToken("test");
    history.push({ pathname: "/app" });
  };

  return (
    <div>
      <button onClick={onApp}>App Dashboard</button>
    </div>
  );
};

export default Home;
