import React from "react";
import { AreaChart, Area, ResponsiveContainer } from "recharts";

const PriceChart = ({ data }) => {
  return (
    <ResponsiveContainer width="100%" height="100%">
      <AreaChart
        data={data}
        margin={{
          top: 0,
          right: 0,
          left: 0,
          bottom: 0,
        }}
      >
        <defs>
          <linearGradient id="splitColor" x1="1" y1="0" x2="0" y2="1">
            <stop offset="40%" stopColor="#2EFAA4" stopOpacity={0.15} />
            <stop offset="60%" stopColor="#01CDFC" stopOpacity={0.15} />
          </linearGradient>
        </defs>
        <Area
          type="monotone"
          dataKey="uv"
          stroke="#16C9BD"
          strokeWidth={2}
          fill="url(#splitColor)"
        />
      </AreaChart>
    </ResponsiveContainer>
  );
};

export default PriceChart;
