import React from "react";
//components
import {
  Button,
  CryptoPrice,
  InputCrypto,
  ResultItem,
} from "../../../../component";
//layout
import { ExchangeLayout } from "../../../../layout";
//assets
import { DownArrow, Information } from "../../../../assets/svg";
//styles
import "./styles.scss";

const Swap = () => {
  return (
    <div className="swap">
      <div className="swap__calculator swap__item">
        <ExchangeLayout title="Swap">
          <InputCrypto select />
          <div className="swap__calculatorArrow">
            <DownArrow />
          </div>
          <InputCrypto />
          <div className="swap__calculatorInfo">
            1 ETH = 0.07543 BTC <Information />
          </div>
          <div className="swap__calculatorBtn">
            <Button title="Connect Wallet" />
          </div>
        </ExchangeLayout>
      </div>
      <div className="swap__info swap__item">
        <ResultItem title="Maximum sold" value="3.15 BTC" />
        <ResultItem title="Price impact" value="0.05%" />
        <ResultItem title="Liquidity Provider Fee" value="0.1 BTC" />
      </div>
      <div className="swap__item">
        <CryptoPrice />
      </div>
    </div>
  );
};

export default Swap;
