import { Switch, Route, Redirect } from "react-router-dom";
// //navigation
import { routesExchange } from "../routes";
import { PublicRoute } from "../components";

const ExchangeNavigation = () => {
  return (
    <Switch>
      {routesExchange.map((route) => (
        <PublicRoute
          key={route.path}
          path={route.path}
          exact={route.exact}
          component={route.component}
        />
      ))}
      <Route path="/app/exchange">
        <Redirect to="/app/exchange/swap" />
      </Route>
      <Route exact path="*" component={() => <> 404</>} />
    </Switch>
  );
};

export default ExchangeNavigation;
