import React, { useState } from "react";
//blocks
import { SelectTokenModal } from "../../blocks";
//assets
import { Arrow } from "../../assets/svg";
import { btc } from "../../assets/icons";
//styles
import "./styles.scss";

const SelectToken = ({ select = false }) => {
  const [showModal, setShowModal] = useState(null);
  const onShowModal = () => {
    setShowModal(true);
  };
  return (
    <>
      <SelectTokenModal showModal={showModal} setShowModal={setShowModal} />
      {select ? (
        <div className="selectedToken" onClick={onShowModal}>
          <div className="selectedToken__name">
            <img src={btc} alt="icon" />
            <p>ETH</p>
          </div>
          <div className="selectedToken__arrow">
            <Arrow />
          </div>
        </div>
      ) : (
        <div className="selectToken" onClick={onShowModal}>
          <p>Select a token</p>
          <div className="selectToken__arrow">
            <Arrow />
          </div>
        </div>
      )}
    </>
  );
};

export default SelectToken;
