import { Switch, Route } from "react-router-dom";
//navigation
import { PrivateRoute, PublicRoute } from "../components";
import { routesPrivate, routesPublic } from "../routes";

const MainNavigation = () => {
  return (
    <Switch>
      {routesPublic.map((route) => (
        <PublicRoute
          key={route.path}
          path={route.path}
          exact={route.exact}
          component={route.component}
        />
      ))}
      {routesPrivate.map((route) => (
        <PrivateRoute
          key={route.path}
          path={route.path}
          exact={route.exact}
          component={route.component}
        />
      ))}
      <Route exact path="*" component={() => <>404</>} />
    </Switch>
  );
};

export default MainNavigation;
