import React, { useState, useRef } from "react";
import clsx from "clsx";
//hooks
import { useOutsideClick } from "../../hooks";
//assets
import { Arrow } from "../../assets/svg";
//styles
import "./styles.scss";

const Selector = ({ list, down = false }) => {
  const refSelector = useRef();
  const [showList, setShowList] = useState(false);

  const toggleShowList = () => {
    setShowList((prev) => !prev);
  };
  const closeList = () => {
    setShowList(false);
  };

  useOutsideClick(refSelector, closeList);

  return (
    <div className="selector" ref={refSelector}>
      <div
        className={clsx("selector__inner", { active: showList })}
        onClick={toggleShowList}
      >
        {list[0]}
        <Arrow />
      </div>
      <div
        className={clsx("selector__list", { downStyle: down, upStyle: !down })}
      >
        {list.map((title, index) => (
          <div className="selector__item" key={index}>
            {title}
          </div>
        ))}
      </div>
    </div>
  );
};

export default Selector;
