import React from "react";
//styles
import "./styles.scss";

const CustomTooltip = ({ active, payload, label }) => {
  return (
    <div className="tooltipInner">
      <p>
        $
        {new Intl.NumberFormat("en", {
          notation: "compact",
          compactDisplay: "short",
        }).format(payload[0]?.value)}
      </p>
      <span>{label}</span>
    </div>
  );
  // }
};

export default CustomTooltip;
