import React from "react";
import clsx from "clsx";
//assets
import { upperArrow, lowerArrow } from "../../assets/icons";
//styles
import "./styles.scss";

const PercentStatistics = ({ lower = false, empty = false }) => {
  return (
    <div className={clsx("percentStatistics", { lower }, { empty })}>
      <img src={lower ? lowerArrow : upperArrow} alt="arrow" />
      12%
    </div>
  );
};

export default PercentStatistics;
