import React from "react";
import { NavLink } from "react-router-dom";
import { useDispatch } from "react-redux";
//actions
import { setMenu } from "../../redux/actions/theme";
//styles
import "./styles.scss";

const NavItem = ({ title, path, Icon }) => {
  const dispatch = useDispatch();

  const onCloseMenu = () => {
    dispatch(setMenu(false));
  };

  return (
    <NavLink to={path} className="navItem" onClick={onCloseMenu}>
      <div className="navItem__inner">
        <Icon />
        <p>{title}</p>
      </div>
    </NavLink>
  );
};

export default NavItem;
