export const getToken = () => {
  return JSON.parse(localStorage.getItem("tokenQ"));
};
export const setToken = (token) => {
  localStorage.setItem("tokenQ", JSON.stringify(token));
};
export const removeToken = () => {
  localStorage.removeItem("tokenQ");
};

export const getThemeMode = () => {
  return JSON.parse(localStorage.getItem("themeQ"));
};
export const setThemeMode = (theme) => {
  localStorage.setItem("themeQ", JSON.stringify(theme));
};
