import { SET_THEME, SET_MENU } from "../../actionTypes/theme";

const initialState = {
  theme: "light",
  openMenu: false,
};

const themeReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_THEME: {
      return {
        ...state,
        theme: action.payload,
      };
    }
    case SET_MENU: {
      return {
        ...state,
        openMenu: action.payload,
      };
    }

    default:
      return state;
  }
};

export default themeReducer;
