import React from "react";
//assets
import { Close } from "../../assets/svg";
//styles
import "./styles.scss";

const ModalHeader = ({ title, closeModal }) => {
  const onCloseModal = () => {
    closeModal(false);
  };
  return (
    <div className="modalHeader">
      <div className="modalHeader__left">
        <div className="modalHeader__title">{title}</div>
      </div>
      <div className="modalHeader__close" onClick={onCloseModal}>
        <Close />
      </div>
    </div>
  );
};

export default ModalHeader;
