import React from "react";
import { useSelector } from "react-redux";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";
//redux
import { themeState } from "../../../redux/reducers/theme/selectors";
//compoents
import { CustomTooltip } from "../../../component";

const LiquidityChart = ({ data }) => {
  const { theme } = useSelector(themeState);
  const axiosStyle = {
    fontFamily: "Barlow",
    fontSize: "12px",
    strokeWidth: 0,
  };
  const strokeTheme = theme === "light" ? "#95A5B0" : "#888F95";
  return (
    <ResponsiveContainer width="100%" height="100%">
      <AreaChart
        data={data}
        margin={{
          top: 15,
          right: 10,
          left: -28,
          bottom: 0,
        }}
      >
        <CartesianGrid
          strokeDasharray="5 5"
          vertical={false}
          stroke={strokeTheme}
        />
        <XAxis
          dataKey="name"
          style={{ ...axiosStyle }}
          tick={{
            fill: strokeTheme,
          }}
          dy={9}
          tickCount={2}
        />
        <YAxis
          style={{ ...axiosStyle }}
          tickFormatter={(value) =>
            new Intl.NumberFormat("en", {
              notation: "compact",
              compactDisplay: "short",
            }).format(value)
          }
          tick={{ fill: strokeTheme }}
        />
        {/* <Tooltip
          contentStyle={{
            borderRadius: 8,
            boxShadow: " 0px 9px 35px rgba(25, 29, 28, 0.08)",
            border: "none",
          }}
        /> */}
        <Tooltip content={<CustomTooltip />} />
        <defs>
          <linearGradient id="splitColor" x1="1" y1="0" x2="0" y2="1">
            <stop offset="40%" stopColor="#2EFAA4" stopOpacity={0.15} />
            <stop offset="60%" stopColor="#01CDFC" stopOpacity={0.15} />
          </linearGradient>
        </defs>

        <Area
          type="monotone"
          dataKey="uv"
          stroke="#16C9BD"
          strokeWidth={3}
          // fill="#16C9BD"
          // fillOpacity="0.1"
          fill="url(#splitColor)"
          activeDot={{
            r: 8,
            strokeWidth: 7,
            stroke: theme === "light" ? "#fff" : "#313538",
          }}
        />
      </AreaChart>
    </ResponsiveContainer>
  );
};

export default LiquidityChart;
