import React from "react";
import { useHistory } from "react-router-dom";
//blocks
import { FilterTable } from "../../../blocks";
//components
import { CashName, IconButton, PoolItem, RefreshBtn } from "../../../component";
//assets
import { Filter } from "../../../assets/svg";
import { download, swap } from "../../../assets/icons";
//styles
import "./styles.scss";

const PoolTable = () => {
  const history = useHistory();
  const headerData = [
    "Pool",
    "Network",
    "USD Price",
    "Liquidity",
    "24h Volume",
    "APY",
  ];
  const tableData = [
    "BTC Native",
    "Bitcoin",
    "$46,345.456",
    "$38.10 M",
    "$0.00",
    "18%",
  ];

  const navigationPool = () => {
    history.push({ pathname: "/app/pool" });
  };

  return (
    <div className="poolTable">
      <div className="poolTable__header">
        <FilterTable />
      </div>
      <div className="poolTable__innerMobile">
        {Array.from({ length: 20 }).map((_, index) => (
          <PoolItem key={index} />
        ))}
      </div>
      <div className="poolTable__inner">
        <table>
          <thead>
            <tr>
              {headerData.map((title) => (
                <td key={title}>
                  {title} <Filter />
                </td>
              ))}
              <td style={{ width: "300px" }}>
                <RefreshBtn />
              </td>
            </tr>
          </thead>
          <tbody>
            {Array.from({ length: 20 }).map((_, index) => (
              <tr key={index}>
                {tableData.map((item, index) =>
                  index === 0 ? (
                    <td
                      key={index}
                      onClick={navigationPool}
                      style={{ cursor: "pointer" }}
                    >
                      <CashName />
                    </td>
                  ) : (
                    <td key={index}>{item}</td>
                  )
                )}
                <td>
                  <div className="tableRowBtn">
                    <div className="tableRowBtn__left">
                      <IconButton title="Swap" icon={swap} />
                    </div>
                    <div className="tableRowBtn__right">
                      <IconButton
                        title="Add Liquidity"
                        icon={download}
                        unFill={true}
                      />
                    </div>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default PoolTable;
