import React, { Fragment } from "react";
import { useSelector, useDispatch } from "react-redux";
import clsx from "clsx";
//selectors
import { themeState } from "../../../redux/reducers/theme/selectors";
//actions
import { setTheme, setMenu } from "../../../redux/actions/theme";
//components
import {
  NavList,
  NavItem,
  NavLine,
  ThemeButton,
  Selector,
} from "../../../component";
//utils
import { setThemeMode } from "../../../utils/localStorage";
//assets
import {
  CloseBig,
  Dashboard,
  Exchange,
  Pools,
  Settings,
  Statistics,
  Support,
} from "../../../assets/svg";
import { logo } from "../../../assets/icons";
//styles
import "./styles.scss";

const NavBar = () => {
  const navbarMenu = [
    {
      title: "Dashboard",
      Icon: Dashboard,
      path: "/app/dashboard",
      type: "element",
    },
    {
      title: "Swap",
      Icon: Exchange,
      path: "/app/exchange/swap",
      type: "element",
    },
    // {
    //   title: "Exchange",
    //   Icon: Exchange,
    //   path: "/app/exchange",
    //   type: "list",
    //   list: [
    //     {
    //       title: "Swap",
    //       path: "/app/exchange/swap",
    //     },
    //     {
    //       title: "Add Liquidity",
    //       path: "/app/exchange/addLiquidity",
    //     },
    //     {
    //       title: "Withdraw",
    //       path: "/app/exchange/withdraw",
    //     },
    //     {
    //       title: "Manage Liquidity",
    //       path: "/app/exchange/manageLiquidity",
    //     },
    //     {
    //       title: "Pending Deposit",
    //       path: "/app/exchange/pendingDeposit",
    //     },
    //     {
    //       title: "Cross-chain Savings",
    //       path: "/app/exchange/crossSavings",
    //     },
    //   ],
    // },
    {
      title: "Pools",
      Icon: Pools,
      path: "/app/pools",
      type: "element",
    },
    {
      title: "Statistics",
      Icon: Statistics,
      path: "/app/statistics",
      type: "element",
    },
    {
      type: "line",
    },
    {
      title: "Settings",
      Icon: Settings,
      path: "/app/settings",
      type: "element",
    },
    {
      title: "Support",
      Icon: Support,
      path: "/app/support",
      type: "element",
    },
  ];
  const lang = ["English", "Franch"];
  const currency = ["USD", "UAH", "EUR"];

  const dispatch = useDispatch();
  const { theme, openMenu } = useSelector(themeState);

  const onSetTheme = (value) => {
    dispatch(setTheme(value));
    setThemeMode(value);
  };

  const toggleMenu = () => {
    dispatch(setMenu(!openMenu));
  };

  const toggleTheme = () => {
    if (theme === "light") {
      onSetTheme("dark");
      document.body.classList.add("darkMode");
    } else if (theme === "dark") {
      onSetTheme("light");
      document.body.classList.remove("darkMode");
    }
  };

  return (
    <>
      <div
        className={clsx("navBar__overlay", { active: openMenu })}
        onClick={toggleMenu}
      />
      <div className={clsx("navBar", { active: openMenu })}>
        <div className="navBar__logo">
          <div className="navBar__close" onClick={toggleMenu}>
            <CloseBig />
          </div>
          <img src={logo} alt="THORCHAIN" />
          <p>THORCHAIN</p>
        </div>
        <div className="navBar__menu">
          {navbarMenu.map((item, index) => (
            <Fragment key={index}>
              {item.type === "element" && <NavItem {...item} key={item.path} />}
              {item.type === "list" && <NavList {...item} key={item.path} />}
              {item.type === "line" && <NavLine {...item} key={item.path} />}
            </Fragment>
          ))}
        </div>
        <div className="navBar__settings">
          <div className="navBar__selectors">
            <div className="navBar__lang">
              <Selector list={lang} />
            </div>
            <div className="navBar__currency">
              <Selector list={currency} />
            </div>
          </div>
          <div className="navBar__theme">
            <ThemeButton theme={theme} onClick={toggleTheme} />
            <p>{theme === "dark" ? "Light mode" : "Dark mode"}</p>
          </div>
        </div>
      </div>
    </>
  );
};

export default NavBar;
