import React from "react";
import { useSelector } from "react-redux";
import {
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
  BarChart,
  Bar,
} from "recharts";
//redux
import { themeState } from "../../../redux/reducers/theme/selectors";
//compoents
import { CustomTooltip } from "../../../component";

const VolumeChart = ({ data }) => {
  const { theme } = useSelector(themeState);
  const axiosStyle = {
    fontFamily: "Barlow",
    fontSize: "12px",
    strokeWidth: 0,
  };
  const strokeTheme = theme === "light" ? "#95A5B0" : "#888F95";

  return (
    <ResponsiveContainer width="100%" height="100%">
      <BarChart
        width={500}
        height={300}
        data={data}
        margin={{
          top: 15,
          right: 10,
          left: -28,
          bottom: 0,
        }}
      >
        <CartesianGrid
          strokeDasharray="5 5"
          vertical={false}
          stroke={strokeTheme}
        />
        <XAxis
          dataKey="name"
          style={{ ...axiosStyle }}
          tick={{
            fill: strokeTheme,
          }}
          dy={9}
          tickCount={2}
        />
        <YAxis
          style={{ ...axiosStyle }}
          tickFormatter={(value) =>
            new Intl.NumberFormat("en", {
              notation: "compact",
              compactDisplay: "short",
            }).format(value)
          }
          tick={{ fill: strokeTheme }}
        />
        <Tooltip cursor={false} content={<CustomTooltip />} />
        <Bar dataKey="uv" fill="#16c9bd" barSize={6} radius={4} />
      </BarChart>
    </ResponsiveContainer>
  );
};

export default VolumeChart;
