import React from "react";
import { NavLink } from "react-router-dom";
//assets
import { Arrow } from "../../assets/svg";
//styles
import "./styles.scss";

const NavList = ({ path, Icon, title, list }) => {
  return (
    <>
      <NavLink to={path} className="navList">
        <div className="navList__inner">
          <Icon />
          <p>{title}</p>
          <Arrow />
        </div>
      </NavLink>
      <div className="navList__box">
        {list.map((item) => (
          <NavLink to={item.path} className="navList__item" key={item.title}>
            {item.title}
          </NavLink>
        ))}
      </div>
    </>
  );
};

export default NavList;
